<template>
  <div>
    <!-- <button id="menu" :class="{ 'toggled': this.menu }">≡</button> -->
    <!-- ≡, ⋮ -->
    <div id="mininav">
      <div style="width:100%; display:flex; flex-direction:row; justify-content:flex-start; align-items:center;">
        <div v-show="this.user != null" style="color:white; padding:0 15px;" :style="this.width >= 768 ? {'margin-right':'auto'} : {}">{{ this.name }}</div>
        <Hamburger ref="hamburger" @toggled="this.hamburger=$event" style="transform:scale(75%); margin-left:auto; padding-right:5px;"/>
      </div>
      <div v-show="this.hamburger" style="display:flex; flex-direction:column; justify-content:space-evenly; align-items:stretch; background-color:black; width:100%;">
        <div v-show="this.user == null" :class="{'active': this.tabs['signup'] === true}" class="tab" @click="this.goToTab('signup')">Sign Up</div>
        <div v-show="this.user == null" :class="{'active': this.tabs['signin'] === true}" class="tab" @click="this.goToTab('signin')">Log In</div>
        <div v-show="this.user != null" class="tab" @click="() => {if (!this.loading) {this.signOut()}}">Log Out</div>
      </div>
    </div>
    <div id="nav">
      <div v-show="this.user != null" style="color:white; padding:0 15px; margin-right:auto;">{{ this.name }}</div>
      <div v-show="this.user == null" :class="{'active': this.tabs['signup'] === true}" class="tab" @click="this.goToTab('signup')">Sign Up</div>
      <div v-show="this.user == null" :class="{'active': this.tabs['signin'] === true}" class="tab" @click="this.goToTab('signin')">Log In</div>
      <div v-show="this.user != null" class="tab" @click="() => {if (!this.loading) {this.signOut()}}">Log Out</div>
    </div>
    <div style="top:40px; height:40px;"></div>
    <div v-show="this.tabs['signup'] || this.tabs['signin'] || this.card !== null" style="position:fixed; top:0; left:0; width:100vw; height:100vh; z-index:2; background-color:rgba(0,0,0,0.5)" @click="() => {this.card = null; delete this.tabs['signin']; delete this.tabs['signup'];}"></div>
    <Signup ref="signup" v-show="this.tabs['signup']" @signedIn="(result) => {this.signedIn(result)}"/>
    <Signin ref="signin" v-show="this.tabs['signin']" @signedIn="(result) => {this.signedIn(result)}"/>
    <!-- <router-view/> -->
    <div v-show="this.user === null" style="display:flex; flex-direction:column; justify-content:flex-start; width:100%;">
      <h1 style="padding:15px; color:var(--mainCol);">Welcome to Board Of States!</h1>
      <h4 style="padding:15px; color:var(--mainCol);">This is an experimental project, developed on purpose of demonstrating technical availabilities of several platforms, and personal skills as well. Although the service is freely available to anyone who may wish to use it for their own projects, please beware that there may exist bugs or missing features in this software... So what can you do in here?</h4>
      <h2 style="padding:15px; color:var(--mainCol);">You can <span style="color:var(--mainOne);" class="textshadow">CREATE</span>, <span style="color:var(--mainOne);" class="textshadow">CHANGE</span> and <span style="color:var(--mainOne);" class="textshadow">SHARE</span> the work plans for your projects</h2>
      <h3 style="padding:15px; color:var(--mainCol);">The basic <span style="color:var(--mainOne);" class="textshadow">TODO</span>, <span style="color:var(--mainOne);" class="textshadow">DOING</span> and <span style="color:var(--mainOne);" class="textshadow">DONE</span> structure lets you and your colleagues determine current state of your jobs</h3>
      <h4 style="padding:15px; color:var(--mainCol);">The ability to add <span style="color:var(--mainOne);" class="textshadow">COMMENTS</span> under each job provides your team an easy way to discuss the ongoing details about any job</h4>
      <h2 style="padding:15px; color:var(--mainCol);">Sign-up now! And keep your work organized!</h2>
    </div>
    <div v-show="this.user !== null && this.card === null" id="mid" style="display:flex; flex-direction:column;">
      <div style="display:flex; flex-direction:row; justify-content:space-evenly;">
        <div style="flex-basis:0; flex-grow:1; padding-top:5px;" v-for="(type, index) in this.types" :key="index" @click="() => {if (!this.loading && this.page !== null) {this.type = index}}">
          <div v-if="this.page === null" style="color: var(--mainTwo); font-weight:bold;" :style="[this.type === index || this.width >= 768 ? {'border-bottom':'5px solid var(--mainOne)'} : {}]">{{ type }}</div>
          <div v-else style="color: var(--mainOne); font-weight:bold;" :style="[this.type === index || this.width >= 768 ? {'border-bottom':'5px solid var(--mainOne)'} : {}]">{{ type }}</div>
          <!-- <div style="color: var(--mainOne); font-weight:bold;" :style="[this.type === index || this.width >= 768 ? {'border-bottom':'5px solid var(--mainOne)'} : {}]">{{ type }}</div> -->
        </div>
        <div style="flex-basis:0; flex-grow:1; padding-top:5px;" @click="() => {if (!this.loading) {this.type = 'board'}}">
          <div style="color: var(--mainOne); font-weight:bold;" :style="[this.type === 'board' || this.width >= 768 ? {'border-bottom':'5px solid var(--mainOne)'} : {}]">board</div>
        </div>
      </div>
      <div style="display:flex; flex-direction:row; justify-content:space-evenly;">
        <div v-show="this.width >= 768 || this.type === index" style="flex-basis:0; flex-grow:1; display:flex; flex-direction:column; justify-content:flex-start;" v-for="(type, index) in this.types" :key="index">
          <div :id="card" style="display:flex; flex-direction:row; justify-content:space-between; color: var(--mainCol); background-color:var(--mainOne); margin:5px 5px 0px 5px; padding-top:10px; padding-bottom:10px; text-align:left; padding:10px;" v-show="cards[card]['type'] === index" v-for="(card, indexTwo) in Object.keys(this.cards)" :key="indexTwo">
            <div style="flex-grow:1; padding:5px;" @click="() => {this.card = card; this.cardContent = this.cards[card]['content']; this.cardType = this.cards[card]['type']; this.cardComment = this.cards[card]['comment'];}">{{ this.cards[card]['content'] }}</div>
          </div>
          <input style="color: var(--mainOne); background-color:var(--mainCol); border: 5px solid var(--mainOne); margin:5px 5px 0px 5px; padding-top:10px; padding-bottom:10px; text-align:left; padding:10px; border-radius:0;" v-show="index === 0 && this.page !== null" @keydown.enter="($event) => {if (!this.loading) {this.newJob(this.page, $event)}}" placeholder="+ new job">
        </div>
        <div v-show="this.width >= 768 || this.type === 'board'" style="flex-basis:0; flex-grow:1; display:flex; flex-direction:column; justify-content:flex-start;">
          <div style="color: var(--mainCol); background-color:var(--mainOne); margin:5px 5px 0px 5px; padding-top:10px; padding-bottom:10px; text-align:left; padding:15px; display:flex; flex-direction:column; justify-content:flex-start; align-items:stretch;" v-for="(page, index) in Object.keys(this.pages)" :key="index" @click="() => {if (!this.loading && this.page !== page) {this.fetchBoard(page)}}">
            <div style="display:flex; flex-direction:row; justify-content:space-between; align-items:center;">
              <div>{{ this.pages[page] }}</div>
              <div v-show="this.page === page && this.user.email === this.admin" @click="() => {if (!this.loading) {this.deleteBoard(page)}}">x</div>
            </div>
            <div style="border-radius:5px; padding:5px; margin-left:0; margin-right:0; margin-top:15px; background-color:var(--mainCol); color:var(--mainOne);" v-show="this.page === page">{{ '👑 ' + this.admin.split('@')[0] }}</div>
            <div style="border-radius:5px; padding:5px; margin-left:0; margin-right:0; margin-top:5px; background-color:var(--mainCol); color:var(--mainOne); display:flex; flex-direction:row; justify-content:space-between; align-items:stretch;" v-show="this.page === page" v-for="(user, index) in Object.keys(this.users)" :key="index">
              <div >{{ '🛠️ ' + user.split('@')[0] }}</div>
              <div v-show="this.user.email === this.admin" style="color:red;" @click="() => {if (!this.loading) {this.deleteUser(this.page, user)}}">x</div>
            </div>
            <input style="border:none; padding:5px; padding-left:10px; margin-left:0; margin-right:0; margin-top:5px; background-color:var(--mainCol); color:var(--mainOne);" v-show="this.page === page && this.user.email === this.admin" placeholder="+ new user via email address" @keydown.enter="($event) => {if (!this.loading) {this.newUser(this.page, $event)}}">
          </div>
          <input style="color: var(--mainOne); background-color:var(--mainCol); border: 5px solid var(--mainOne); margin:5px 5px 0px 5px; padding-top:10px; padding-bottom:10px; text-align:left; padding:10px; border-radius:0;" @keydown.enter="($event) => {if (!this.loading) {this.newBoard($event)}}" placeholder="+ new board">
        </div>
      </div>
    </div>
    <div v-show="this.user !== null && this.card !== null" style="width:100%; color:var(--mainOne); z-index:4; position:fixed; top:0px; left:0px; background-color:var(--sideOne); display:flex; flex-direction:column; justify-content:flex-start; align-items:stretch;">
      <div style="top:39px; height:39px;"></div>
      <!-- <div style="padding:15px; text-align:left; background-color:var(--mainOne); color:white; height:15px; display:flex; flex-direction:row; justify-content:flex-end;">
        <div class="link" @click="this.card = null">{{ 'Return to ' + this.pages[this.page] + '?' }}</div>
      </div> -->
      <div style="padding:15px; text-align:left; height:20px; overflow:auto; font-weight:bold; background-color:white;" @click="() => {if (!this.loading) {this.card = null}}">{{ this.cardContent }}</div>
      <div style="display:flex; flex-direction:row; justify-content:space-evenly;">
        <!-- <div style="padding-top:15px; padding-bottom:15px; text-align:center; flex-base:0; flex:2; background-color:rgb(150,150,150); color:white;" @click="() => {if (!this.loading) {this.card = null}}">close</div> -->
        <div style="padding-top:15px; padding-bottom:15px; text-align:center; flex-base:0; flex:2;" :style="[this.cardType === index ? {'background-color':'var(--mainOne)', 'color':'var(--mainCol)'} : {'background-color':'var(--mainTwo)', 'color':'var(--mainCol)'}]" v-for="(type, index) in this.types" :key="index" @click="() => {if (!this.loading && this.cards[this.card]['type'] !== index) {this.updateJobType(this.page, this.card, index)}}">{{ type }}</div>
        <div style="padding-top:15px; padding-bottom:15px; text-align:center; flex-base:0; flex:2; background-color:rgb(200,20,20); color:white;" @click="() => {if (!this.loading) {this.deleteJob(this.page, this.card)}}">delete</div>
      </div>
      <div style="display:flex; flex-direction:column; justify-content:space-evently; text-align:left;">
        <!-- <div style="flex: 0 0 auto; padding:15px; font-weight:bold; background-color:white;">comments:</div> -->
        <div id="comments" style="flex: 1 1 auto; overflow-y:auto; height:calc(100vh - 200px + 15px); align-items:left;">
          <div class="comment textshadow" v-for="(comment, index) in this.cardComment" :key="index" style="padding:5px 15px 5px 15px;">{{ comment }}</div>
        </div>
        <input style="flex: 0 0 auto; padding:15px; border: none; border-radius:0px; margin:0;" placeholder="+ new comment" @keydown.enter="($event) => {if (!this.loading) {this.newComment(this.page, this.card, $event)}}">
      </div>
    </div>
    <div v-show="this.loading" id="loading" style="position:fixed; top:0; left:0; height:100vh; width:100vw; background-color:rgba(0,0,0,0.5); z-index:6;">
      <img src="./assets/loading.svg" style="position:relative; z-index:7; width:30%; height20%; top:50%; left:0; transform:translateY(-50%);">
    </div>
    <Animated/>
  </div>
</template>

<script>
import Hamburger from './components/Hamburger.vue';
import Signup from './components/Signup.vue';
import Signin from './components/Signin.vue';
import Animated from './components/Animated.vue';

export default {
  name: "App",
  components: { Hamburger, Signup, Signin, Animated },
  data() {
    return {
      loading: false,
      width: 0,
      height: 0,
      name: "",
      admin: "",
      user: null,
      users: {},
      page: null,
      pages: {},
      type: "board",
      types: ["todo", "doing", "done"],
      cardContent: "",
      cardType: "",
      cardComment: [],
      card: null,
      cards: {},
      links: [],
      tabs: { "signin": false, "signup": false },
      hamburger: false,
      toggledBack: false,
    };
  },
  computed: {
    
  },
  methods: {
    subscribe() {
      db.collection("pages").where("state", "==", "CA")
      .onSnapshot((snapshot) => {
        snapshot.docChanges().forEach((change) => {
          if (change.type === "added") {
              console.log("New city: ", change.doc.data());
          }
          if (change.type === "modified") {
              console.log("Modified city: ", change.doc.data());
          }
          if (change.type === "removed") {
              console.log("Removed city: ", change.doc.data());
          }
        });
      });
    },
    goToPage(page) {
      //const query = JSON.parse(JSON.stringify(this.$route.query));
      //query["skip"] = (page - 1) * this.limit
      //this.$router.push({ name: page, query: query })
      this.$router.push({ path: page })
    },
    goToTab(tab) {
      this.$refs.hamburger.toggle(false)
      Object.keys(this.tabs).forEach(key => {
        delete this.tabs[key]
      });
      if (tab !== null) {
        this.tabs[tab] = true
      }
    },
    async signedIn(result) {
      this.loading = true
      this.user = result
      Object.keys(this.tabs).forEach(key => {
        delete this.tabs[key]
      });
      this.$refs.hamburger.toggle(false)
      await this.fetchBoards()
      this.loading = false
    },
    async signOut() {
      this.loading = true
      // await this.user.signOut()
      await auth.signOut()
      this.loading = false
    },
    async fetchBoards() {
      this.loading = true
      this.page = null
      this.type = "board"
      const newPages = {};
      // Object.keys(this.pages).forEach(key => {
      //   delete this.pages[key]
      // });
      this.card = null
      Object.keys(this.cards).forEach(key => {
        delete this.cards[key]
      });
      if (!this.user) {
        this.loading = false
        return
      }
      try {
        await db.collection("pages").where("admin", "==", this.user.email).get()
        .then(snapshot => {
          snapshot.forEach(doc => {
            newPages[doc.id] = doc.data().title
          })
        })
        //await db.collection("pages").where("users", "array-contains", this.user.email).get()
        await db.collection("pages").where("users", "==", { [this.user.email]: true }).get()
        .then(snapshot => {
          snapshot.forEach(doc => {
            newPages[doc.id] = doc.data().title
          })
        })
        this.pages = newPages
      } catch (error) {
        console.log('fetching boards failed', error)
      }
      this.loading = false
    },
    async fetchBoard(boardID) {
      this.loading = true
      if (boardID !== this.page) {
        this.card = null
        Object.keys(this.cards).forEach(key => {
          delete this.cards[key]
        });
        this.page = null
        this.type = "board"
      }
      if (!boardID) {
        this.loading = false
        return
      }
      const newCards = {};
      try {
        await db.collection("pages").doc(boardID).get()
        .then(doc => {
          this.admin = doc.data().admin
          this.users = doc.data().users
        })
        await db.collection("pages").doc(boardID).collection("cards").get()
        .then(snapshot => {
          snapshot.forEach(doc => {
            newCards[doc.id] = { type: doc.data().type, content: doc.data().content, comment: doc.data().comment }
          })
        })
        this.cards = newCards
        let oldPage = "" + this.page
        this.page = boardID;
        if (oldPage !== null && oldPage !== this.page) {
          if (unsubscribePage !== null) {
            unsubscribePage()
          }
          if (unsubscribeCards !== null) {
            unsubscribeCards()
          }
          unsubscribePage = db.collection("pages").doc(this.page)
          .onSnapshot(() => {
            this.fetchBoard(this.page)
          });
          unsubscribeCards = db.collection("pages").doc(this.page).collection("cards")
          .onSnapshot(() => {
            this.fetchBoard(this.page)
          });
        }
        if (this.card && this.card in this.cards) {
          this.cardType = this.cards[this.card]['type']
          this.cardContent = this.cards[this.card]['content']
          this.cardComment = this.cards[this.card]['comment']
          document.getElementById("comments").scrollHeight
        }
      } catch (error) {
        console.log('fetching board failed', error)
      }
      this.loading = false
    },
    async newUser(boardID, event) {
      try {
        this.loading = true
        const toAdd = "" + event.target.value
        const doc = await db.collection("pages").doc(boardID).get()
        const newUsers = doc.data().users
        newUsers[toAdd] = true
        await db.collection("pages").doc(boardID).update({ users: newUsers })
        event.target.value = ""
        //await this.fetchBoard(boardID)
        this.loading = true
      } catch (error) {
        console.log('adding new user failed', error)
      } finally {
        this.loading = false
      }
    },
    async deleteUser(boardID, user) {
      try {
        this.loading = true
        const doc = await db.collection("pages").doc(boardID).get()
        const newUsers = doc.data().users
        delete newUsers[user]
        await db.collection("pages").doc(boardID).update({ users: newUsers })
        //await this.fetchBoard(boardID)
        this.loading = true
      } catch (error) {
        console.log('deleting user failed', error)
      } finally {
        this.loading = false
      }
    },
    async newJob(boardID, event) {
      try {
        this.loading = true
        await db.collection("pages").doc(boardID).collection("cards").add({ content: event.target.value, type: 0 })
        event.target.value = ""
        //await this.fetchBoard(boardID)
        this.loading = true
      } catch (error) {
        console.log('adding new job failed', error)
      } finally {
        this.loading = false
      }
    },
    async newComment(boardID, jobID, event) {
      try {
        this.loading = true
        await db.collection("pages").doc(boardID).collection("cards").doc(jobID).get()
        .then(doc => {
          const comment = doc.data().comment
          if (comment === undefined || comment === null) {
            return db.collection("pages").doc(boardID).collection("cards").doc(jobID).update({ comment: {0: this.user.email.split("@")[0] + ": " + event.target.value} })
          } else {
            comment[Object.keys(comment).length] = this.user.email.split("@")[0] + ": " + event.target.value
            return db.collection("pages").doc(boardID).collection("cards").doc(jobID).update({ comment: comment })
          }
        }).then(doc => {
          event.target.value = ""
          //return this.fetchBoard(boardID)
        })
      } catch (error) {
        console.log('pushing new comment failed', error)
      } finally {
        this.loading = false
      }
    },
    // async editJob(boardID, jobID, content, type) {
    //   try {
    //     this.loading = true
    //     await db.collection("pages").doc(boardID).collection("cards").doc(jobID).set({ content: content, type: type })
    //     await this.fetchBoard(boardID)
    //   } catch (error) {
    //     console.log('editing job failed', error)
    //   } finally {
    //     this.loading = false
    //   }
    // },
    async updateJobType(boardID, jobID, type) {
      try {
        this.loading = true
        await db.collection("pages").doc(boardID).collection("cards").doc(jobID).update({ type: type })
        //await this.fetchBoard(boardID)
      } catch (error) {
        console.log('updating job type failed', error)
      } finally {
        this.loading = false
      }
    },
    async deleteJob(boardID, jobID) {
      try {
        this.loading = true
        await db.collection("pages").doc(boardID).collection("cards").doc(jobID).delete()
        //await this.fetchBoard(boardID)
        this.loading = true
        this.card = null
      } catch (error) {
        console.log('deleting job failed', error)
      } finally {
        this.loading = false
      }
    },
    async newBoard(event) {
      try {
        this.loading = true
        const doc = await db.collection("pages").add({ title: event.target.value, admin: this.user.email, users: {} })
        await db.collection("pages").doc(doc.id).collection("cards").add({ content: "first job", type: 0, comment: {} })
        event.target.value = ""
        await this.fetchBoards()
      } catch (error) {
        console.log('adding new board failed', error)
      } finally {
        this.loading = false
      }
    },
    async deleteBoard(boardID) {
      try {
        this.loading = true
        await db.collection("pages").doc(boardID).collection("cards").get()
        .then(snapshot => {
          snapshot.forEach(doc => {
            doc.ref.delete()
          })
        })
        await db.collection("pages").doc(boardID).delete()
        if (this.page === boardID) {
          this.card = null
          Object.keys(this.cards).forEach(key => {
            delete this.cards[key]
          });
          this.page = null
          this.type = "board"
        }
        await this.fetchBoards()
      } catch (error) {
        console.log('deleting board failed', error)
      } finally {
        this.loading = false
      }
    }
  },
  watch: {
    $route(to, from) {
      this.$refs.hamburger.toggle(false)
    }
    // $route(to, from) {
    //   //console.log("routing...")
    //   this.loading = true
    //   this.clearDetails()
    //   this.inventory.length = 0
    //   this.count = 0
    //   this.pages.length = 0
    //   this.resetToggled()
    //   const path = this.$route.path
    //   //console.log(path)
    //   if (path !== "/") {
    //     this.menu = "passive";
    //     this.loading = false
    //     return
    //   }
    //   let url = ""
    //   if (process.env.NODE_ENV === "development") {
    //     url = "http://localhost:8081"
    //   } else {
    //     url = "/"
    //   }
    //   this.fetchFor(url, this.$route.query).then(data => {this.loading = false}).catch(error => {this.menu = "passive"; this.loading = false})
    // }
  },
  created() {
    
  },
  mounted() {
    this.loading = true
    this.width = window.innerWidth
    this.height = window.innerHeight
    window.addEventListener('resize', () => {this.width = window.innerWidth; this.height = window.innerHeight;})
    auth.onAuthStateChanged(cred => {
      if (cred) {
        // User is signed in, see docs for a list of available properties
        // https://firebase.google.com/docs/reference/js/firebase.User
        this.user = cred;
        this.name = this.user.email.split("@")[0]
      } else {
        this.user = null
      }
      this.fetchBoards()
    })
    // let pageListener = db.collection("pages").where("page", "==", this.page)
    // .onSnapshot(() => {
    //   this.fetchBoard(this.page)
    // });
    // let cardListener = db.collection("pages").doc(this.page).collection("cards")
    // .onSnapshot(() => {
    //   this.fetchBoard(this.page)
    // });
    // window.addEventListener('mouseup', () => {this.carrying = null; console.log(document.querySelectorAll( ":hover" ))})
  }
};
</script>

<style>
:root {
  --specOne: rgb(255, 166, 0);
  --specTwo: rgb(255, 166, 0);
  --specCol: black;
  --mainOne: orange;
  --mainTwo: rgb(138, 90, 0);
