<template>
  <div class="container" @click="toggle(!this.toggled)">
    <div class="bar1" :class="{ 'toggled': this.toggled }"></div>
    <div class="bar2" :class="{ 'toggled': this.toggled }"></div>
    <div class="bar3" :class="{ 'toggled': this.toggled }"></div>
  </div>
</template>

<script>
export default {
  name: 'Hamburger',
  data() {
    return {
      toggled: false,
    }
  },
  props: [],
  emits: ["toggled"],
  methods: {
    toggle(to) {
      this.toggled = to
      this.$emit("toggled", this.toggled)
    }
  },
  watch: {
      
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container {
  display: inline-block;
  cursor: pointer;
}
.bar1, .bar2, .bar3 {
  width: 35px;
  height: 5px;
  background-color: #333;
  margin: 6px 0;
  transition: 0.4s;
}
/* Rotate first bar */
.bar1.toggled {
  transform: rotate(-45deg) translate(-9px, 6px);
}
/* Fade out the second bar */
.bar2.toggled {
  opacity: 0;
}
/* Rotate last bar */
.bar3.toggled {
  transform: rotate(45deg) translate(-8px, -8px);
}
</style>
