<template>
  <div class="signup">
    <form @submit.prevent="this.submit($event)">
      <label for="email">email</label>
      <input type="email" name="email">
      <label for="passwordOne">password</label>
      <input type="password" name="passwordOne">
      <label for="passwordTwo">password again</label>
      <input type="password" name="passwordTwo">
      <label></label>
      <label></label>
      <input type="submit" value="Sign Up">
      <label style="color:orange;">{{ this.feedback }}</label>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      feedback: ""
    }
  },
  props: [],
  emits: ["signedIn"],
  methods: {
    submit(event) {
      let email = event.target.email.value
      let password = event.target.passwordOne.value
      let confirmation = event.target.passwordTwo.value
      if (confirmation !== password) {
        this.feedback = "Passwords don't seem to match."
        return
      }
      auth.createUserWithEmailAndPassword(email, password)
      .then(credentials => {
        let user = credentials.user
        this.$emit('signedIn', user)
        let username = (email.split(".")[0]).replace(/^[a-zA-Z0-9_]/, "").trim()
        user.updateProfile({ displayName: username })
        user.sendEmailVerification()
      })
      .catch((error) => {
        var errorCode = error.code
        var errorMessage = error.message
        this.feedback = errorMessage
      })
    }
  }
}
</script>

