<template>
  <div class="signin">
    <form @submit.prevent="this.submit($event)">
      <label for="email">email</label>
      <input type="email" name="email" id="email">
      <label for="password" id="password">password</label>
      <input type="password" name="password">
      <label></label>
      <label></label>
      <input type="submit" name="submit" value="Sign In">
      <span style="text-align:center; font-size:75%; background-color:var(--mainOne);" id="forgottenLink" @click="this.reset()">Forgotten password?</span>
      <label style="color:orange;">{{ this.feedback }}</label>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      feedback: ""
    }
  },
  props: [],
  emits: ["signedIn"],
  methods: {
    submit(event) {
      let email = event.target.email.value
      let password = event.target.password.value
      auth.signInWithEmailAndPassword(email, password)
      .then(credentials => {
        event.target.email.value = ""
        event.target.password.value = ""
        this.$emit('signedIn', credentials.user)
      })
      .catch((error) => {
        event.target.password.value = ""
        var errorCode = error.code
        var errorMessage = error.message
        this.feedback = errorMessage
      })
    },
    reset() {
      auth.sendPasswordResetEmail(document.getElementById('email').value)
      .then(success => {
        document.getElementById('email').value = ""
        document.getElementById('password').value = ""
        this.feedback = "Please check your email address for further instructions."
      })
      .catch((error) => {
        document.getElementById('password').value = ""
        var errorCode = error.code
        var errorMessage = error.message
        this.feedback = errorMessage
      })
    }
  }
}
</script>

<style>
#forgottenLink:hover {
  color: yellow;
}
</style>